<template>
  <div class="admin-wrapper">
    <div class="table-toggle">
      <button :class="{ active: selected === 'uts' }" @click="selected = 'uts'">Tampilkan UTS</button>
      <button :class="{ active: selected === 'uas' }" @click="selected = 'uas'">Tampilkan UAS</button>
    </div>

    <div class="table-section">
      <h2>Data Nilai {{ selected.toUpperCase() }}</h2>
      <table>
        <thead>
          <tr>
            <th>Nama</th>
            <th>NIM</th>
            <th>Nilai</th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="getSelectedData.length === 0">
            <td colspan="3" class="empty">Belum ada data.</td>
          </tr>
          <tr v-for="(item, index) in getSelectedData" :key="index">
            <td>{{ item.nama }}</td>
            <td>{{ item.nim }}</td>
            <td>{{ item.nilai }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div class="admin-buttons">
      <button @click="downloadExcel('uts')">Download UTS</button>
      <button @click="downloadExcel('uas')">Download UAS</button>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";

const utsData = ref([]);
const uasData = ref([]);
const selected = ref("uts");

const getSelectedData = computed(() => {
  return selected.value === "uts" ? utsData.value : uasData.value;
});

onMounted(() => {
  fetch("https://a96b-103-158-126-212.ngrok-free.app/get-all-scores?jenis=uts")
    .then((res) => res.json())
    .then((data) => {
      utsData.value = data || [];
    });

  fetch("https://a96b-103-158-126-212.ngrok-free.app/get-all-scores?jenis=uas")
    .then((res) => res.json())
    .then((data) => {
      uasData.value = data || [];
    });
});

const downloadExcel = (jenis) => {
  fetch(`https://a96b-103-158-126-212.ngrok-free.app/download-excel?jenis=${jenis}`, {
    headers: { "ngrok-skip-browser-warning": "true" },
  })
    .then((response) => response.blob())
    .then((blob) => {
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `data_nilai_${jenis}.xlsx`;
      document.body.appendChild(a);
      a.click();
      a.remove();
      window.URL.revokeObjectURL(url);
    })
    .catch((err) => {
      console.error("Gagal mengunduh:", err);
      alert("Gagal mengunduh file. Pastikan server aktif dan Ngrok berjalan.");
    });
};
</script>

<style scoped>
.admin-wrapper {
  padding: 2rem;
  font-family: Arial, sans-serif;
}

.admin-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  margin-top: 2rem;
}

.admin-buttons button {
  background-color: #4f46e5;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  font-weight: bold;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.3s;
}

.admin-buttons button:hover {
  background-color: #4338ca;
}

.table-toggle {
  text-align: center;
  margin-bottom: 1rem;
}

.table-toggle button {
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  font-weight: 600;
  border-radius: 0.5rem;
  border: 1px solid #ccc;
  cursor: pointer;
  background-color: white;
}

.table-toggle button.active {
  background-color: #4f46e5;
  color: white;
  border-color: #4f46e5;
}

.table-section {
  overflow-x: auto;
}

h2 {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.25rem;
  color: #333;
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: #f9fafb;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.06);
}

th,
td {
  padding: 0.75rem 1rem;
  text-align: center;
  border-bottom: 1px solid #e5e7eb;
}

thead {
  background-color: #4f46e5;
  color: white;
}

tr:hover {
  background-color: #eef2ff;
}

.empty {
  color: #888;
  font-style: italic;
}
</style>
