<template>
  <main>
    <!-- Tombol Admin -->
    <div class="admin-icon-button" @click="checkPassword">
      <img src="./../assets/images/icon-admin.svg" alt="Admin Icon" />
    </div>

    <article class="row-top">
      <div class="curr-subject">
        <div class="button-icon-container">
          <img class="subject-img" src="./../assets/images/icon-css.svg" alt="subject icon" />
        </div>
        <!-- <img class="subject-img" src=""> -->
        <h2 class="subject-chosen">some Subject</h2>
      </div>
      <div class="light-dark-toggle">
        <div class="sun-container">
          <img src="" alt="picture of sun" />
        </div>
        <label class="light-dark-switch" for="checkbox">
          <input type="checkbox" id="checkbox" />
          <div class="slider round"></div>
        </label>
        <div class="moon-container">
          <img src="" alt="picture of moon" />
        </div>
      </div>
    </article>

    <!-- Quiz menu start -->

    <article class="start-menu">
      <div class="left-content">
        <h1>Let's Get Started!</h1>
        <h1>Quiz Instalasi Penerangan Listrik</h1>
        <p>Bagus Ari Wibowo.</p>
      </div>

      <div class="choices">
        <button id="HTML" class="quiz-type" @click="goToQuiz('uts')">
          <div class="button-icon-container">
            <img src="./../assets/images/icon-uts.svg" alt="html icon" />
          </div>
          UTS
        </button>
        <button id="CSS" class="quiz-type" @click="goToQuiz('uas')">
          <div class="button-icon-container">
            <img src="./../assets/images/icon-uas.svg" alt="css icon" />
          </div>
          UAS
        </button>
      </div>
    </article>

    <!-- Quiz menu end -->
  </main>
</template>

<script setup>
import { useRouter } from "vue-router";
const router = useRouter();

function goToQuiz(type) {
  router.push(`/${type}`);
}
window.addEventListener("DOMContentLoaded", () => {
  const toggleSwitch = document.querySelector('.light-dark-switch input[type="checkbox"]');
  if (!toggleSwitch) return;

  toggleSwitch.addEventListener("change", (event) => {
    if (event.target.checked) {
      document.documentElement.setAttribute("data-theme", "dark");
    } else {
      document.documentElement.setAttribute("data-theme", "light");
    }
  });
});

function checkPassword() {
  const correctPassword = "12345"; // ganti dengan password rahasia kamu
  const input = prompt("Masukkan Password Admin:");

  if (input === correctPassword) {
    router.push("/admin");
  } else {
    alert("Password salah!");
  }
}
</script>

<style scoped>
.admin-icon-button {
  position: absolute;
  top: 1.25rem;
  left: 1.25rem;
  width: 48px;
  height: 48px;
  background-color: #4f46e5;
  border-radius: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  transition: background-color 0.3s ease;
}

.admin-icon-button:hover {
  background-color: #3730a3;
}

.admin-icon-button img {
  width: 24px;
  height: 24px;
  filter: invert(1); /* putih di atas latar biru */
}
</style>
